@import "https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  background: #4070f4;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.wrapper {
  background: #fff;
  border-radius: 6px;
  width: 100%;
  max-width: 430px;
  padding: 34px;
  position: relative;
  box-shadow: 0 5px 10px #0003;
}

.wrapper h2 {
  color: #333;
  font-size: 22px;
  font-weight: 600;
  position: relative;
}

.wrapper h2:before {
  content: "";
  background: #4070f4;
  border-radius: 12px;
  width: 28px;
  height: 3px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.wrapper form {
  margin-top: 30px;
}

.wrapper form .input-box {
  margin: 10px 0;
}

form .input-box input {
  color: #333;
  border: 1.5px solid #c7bebe;
  border-bottom-width: 2.5px;
  border-radius: 6px;
  outline: none;
  width: 100%;
  min-height: 52px;
  padding: 0 15px;
  font-size: 17px;
  font-weight: 400;
  transition: all .3s;
}

.input-box input:focus, .input-box input:valid {
  border-color: #4070f4;
}

form .policy {
  align-items: center;
  display: flex;
}

form h3 {
  color: #707070;
  margin-left: 10px;
  font-size: 14px;
  font-weight: 500;
}

.input-box.button input {
  color: #fff;
  letter-spacing: 1px;
  cursor: pointer;
  background: #4070f4;
  border: none;
}

.input-box.button input:hover {
  background: #0e4bf1;
}

form .text h3 {
  color: #333;
  text-align: center;
  width: 100%;
}

form .text h3 a {
  color: #4070f4;
  text-decoration: none;
}

form .text h3 a:hover {
  text-decoration: underline;
}

